#Skills {
  text-align: center;
  background-color: #f1ece9;
}

#Skills .imageWrapper {
  height: 150px;
  display: flex;
  justify-content: center;
}

#Skills .skillBox {
  min-height: 250px;
  display: flex;
}
#Skills .container{
    display: flex;
}
#Skills .image {
  place-self: center;
 margin: 10px;
  width: 5vw;
  height:auto;
}
#Skills .skills-container{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
#Skills .skills-container div{
    display: grid;
    grid-template: auto / auto auto ;
    justify-content: space-evenly;
}
#Skills .skills-container h2 {
    display: flex;
    flex-direction: column;
}
.skill-card{
    display: flex;
    flex-direction: column;
}

#Skills #database{
    display: grid;
    grid: auto auto / auto auto;
}
#Skills #frontend{
    display: grid;
    grid: auto auto / auto auto auto;
}
#Skills #database{
    display: grid;
    grid: auto auto / auto auto;
}
